<script>
import { Line } from 'vue-chartjs'

import { getStyle, hexToRgba } from '@coreui/coreui/dist/js/coreui-utilities'
import { CustomTooltips } from '@coreui/coreui-plugin-chartjs-custom-tooltips'
import { serverBus } from '@/main'

function random (min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

export default {
  extends: Line,
  props: ['height'],

  data(){
    return{
      datas: []
    }
  },

  mounted () {
    this.getData()
  },

  created(){
    serverBus.$on('selectRange', (val) => {
      if(val === 1) this.renderChartNow(this.datas.all_times)
      if(val === 2) this.renderChartNow(this.datas.current_year)
      if(val === 3) this.renderChartNow(this.datas.current_month)
    })
  },

  methods: {
    getData(){
      this.$http({
        url:  '/api/v1/admin/reports/invoices/1',
        data: this.form,
        method:'GET',
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
      .then(response => {
        this.datas = response.data
        this.renderChartNow(response.data.current_month)
      })
      .catch((error) => {
        error.response.data.map((m) => {
          this.$toasted.show(m, { 
            position:'top-right', 
            duration: 5000,
            type: 'error',
            closeOnSwipe: true
          })
        })
      })
    },

    renderChartNow(data){
      const brandInfo = getStyle('--info') || '#20a8d8'

      let labels      = Object.keys(data)
      let dataDisplay = Object.values(data).map((x) => { return parseInt(x.length) })
          // dataDisplay = Math.max(dataDisplay)

      this.renderChart({
        labels: labels,
        datasets: [
          {
            label: 'My First dataset',
            backgroundColor: hexToRgba(brandInfo, 10),
            borderColor: brandInfo,
            pointHoverBackgroundColor: '#fff',
            borderWidth: 2,
            data: dataDisplay
          }
        ]
      }, {
        tooltips: {
          enabled: false,
          custom: CustomTooltips,
          intersect: true,
          mode: 'index',
          position: 'nearest',
          callbacks: {
            labelColor: function (tooltipItem, chart) {
              return { backgroundColor: chart.data.datasets[tooltipItem.datasetIndex].borderColor }
            }
          }
        },
        maintainAspectRatio: false,
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            gridLines: {
              drawOnChartArea: false
            }
          }],
          yAxes: [{
            ticks: {
              beginAtZero: true,
              maxTicksLimit: 5,
              stepSize: Math.ceil(Math.max(...dataDisplay) / 2),
              max: Math.max(...dataDisplay) + 5
            },
            gridLines: {
              display: true
            }
          }]
        },
        elements: {
          point: {
            radius: 0,
            hitRadius: 10,
            hoverRadius: 4,
            hoverBorderWidth: 3
          }
        }
      })
    }
  }
}
</script>
