<template>
  <b-card>
    <template v-slot:header>
      <h6 class="mb-0">
        Last Ranked Users
        <i class="ml-1 fa fa-trophy"></i>
      </h6>
    </template>
    <p>Users that were ranked on the current month</p>
    <b-table
      :hover="hover"
      :striped="striped"
      :bordered="bordered"
      :small="small"
      :fixed="fixed"
      responsive="sm"
      :items="items"
      :fields="fields"
      :per-page="perPage"
      :filter="filter"
      :sort-by.sync="sortBy"
      :sort-direction="sortDirection"
      class="mt-3"
      :busy="loading"
    >
      <div slot="table-busy" class="text-center text-danger my-2">
        <strong>Loading...</strong>
      </div>

      <template v-slot:cell(username)="data">
        <span class="badge badge-primary">{{data.item.username}}</span>
      </template>

      <template v-slot:cell(rank)="data">
        <span class="badge badge-warning">{{data.item.rank}}</span>
      </template>

      <template v-slot:cell(rank_points)="data">
        <span class="badge badge-default">{{data.item.vault.career_current_points}}</span>
      </template>

      <template v-slot:cell(actions)="data">
        <router-link :to="'users/edit/' + data.item.uuid" class="btn btn-sm btn-dark ml-1">
          Edit
          <i class="fa fa-pencil" />
        </router-link>
      </template>
    </b-table>
  </b-card>
</template>

<script>
export default {
  name: "users-ranks",
  props: {
    server: { type: String },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: true
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      items: [],
      fields: [
        { key: "id", sortable: true, label: "ID"  },
        { key: "username", sortable: false, label: "Username" },
        { key: "email", sortable: false, label: "Email" },
        { key: "rank", sortable: false, label: "Rank", class: "text-left" },
        {
          key: "rank_points",
          sortable: true,
          label: "Rank Points",
          class: "text-center"
        },
        {
          key: "rank_updated_at",
          sortable: true,
          label: "Ranked at",
          class: "text-right"
        },
        {
          key: "actions",
          label: "Actions",
          class: "text-right"
        }
      ],
      pageNo: 1,
      currentPage: 1,
      perPage: 30,
      totalRows: 0,
      pageCount: 1,
      show: true,
      loading: false,
      filter: null,
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 100, 250, 500, 1000],
      sortBy: null,
      sortDesc: "desc",
      sortDirection: "asc"
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.loading = true;
      this.$http
        .get(`/api/v1/admin/users/ranks`, {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        })
        .then(response => {
          this.items = response.data;
          this.loading = false;
        })
        .catch(error => {
          error.response.data.map(m => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true
            });
            this.loading = false;
          });
        });
    }
  }
};
</script>